.card{
    background-color: rgb(80, 65, 95);
    padding: 20px;
    border-radius: 10px;
    width: 100%;
    margin-right: 20px;
    overflow: hidden;
    display: block;
}

.main{
    padding: 20px;
}

.judul-card{
    color: white;
    margin-top: -5px;
}

.card-bttn{
    background-color: rgb(82, 27, 68);
    color: aliceblue;
    padding: 5px 20px;
    outline: none;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-left: 9px;
}

.container{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
}

.card-layout{
    display: flex;
}