.card{
    background-color: rgb(112, 42, 42);
    transition: background-color 0.3s ease;
    padding: 20px;
    border-radius: 10px;
    width: 250px;
    margin-right: 20px;
    overflow: hidden;
    display: block;
}

.color-change{
    background-color: rgb(78, 55, 100);
    padding: 20px;
    border-radius: 10px;
    width: 250px;
    margin-right: 20px;
    overflow: hidden;
    display: block;
}

.card image{
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    display: block;
}

.main{
    padding: 20px;
}

.card-layout{
    display: flex;
}

.container{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
}